import React,{useState} from 'react';  
import "./portfolio.scss";
import Fportfoliolist from "../fportfoliolist/Fportfoliolist";



export default function Portfolio() {
    const [ selected, setSelected ] = useState( "featured" )
    const list = [
         {
             id: "featured",
             title: "Featured",
         },
          {
             id: "web",
             title: "Web App",
         },
          {
             id: "mobile",
             title: "Mobile App",
         },
          {
             id: "design",
             title: "Design",
         },
          {
             id: "content",
             title: "Content",
         },
];
    return (
        <div className="portfolio" id="portfolio">
            <h1>Games</h1>
            <ul>
                {list.map((item) =>(
                    <Fportfoliolist 
                    title={item.title} 
                    active ={ selected === item.id}
                    setSeclected={setSelected}
                    id={item.id}
                    />
                ))}
            </ul>
            <div className="container">
                <div className="item">
                    <img src="assets/hiendangcapnhap.png" alt=""/>
                    <h3>updating...</h3>
                </div>
                <div className="item">
                    <img src="assets/hiendangcapnhap.png" alt=""/>
                    <h3>updating...</h3>
                </div>
                <div className="item">
                    <img src="assets/hiendangcapnhap.png" alt=""/>
                    <h3>updating...</h3>
                </div>
                 <div className="item">
                    <img src="assets/hiendangcapnhap.png" alt=""/>
                    <h3>updating...</h3>
                </div>
                <div className="item">
                    <img src="assets/hiendangcapnhap.png" alt=""/>
                    <h3>updating...</h3>
                </div>
                <div className="item">
                    <img src="assets/hiendangcapnhap.png" alt=""/>
                    <h3>updating...</h3>
                </div>
            

            </div>
           
        </div>
    )
}
